<template>
  <div class="m-dialog-wrap" :class="visible && 'active'">
    <div class="m-dialog" :class="fadeAnimation ? 'fade-in' : 'fade-out'">
      <div class="m-dialog-header">
        {{title}}
      </div>
      <div class="m-dialog-content">
        <slot name="content"></slot>
      </div>
      <div class="m-dialog-footer">
        <button class="m-btn" @click="handleCancel" >取消</button>
        <button class="m-btn" @click="handleOk" >确定</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    visible: {
      type: Boolean
    },
    fadeAnimation: {
      type: Boolean
    },
    title: {
      type: String,
      default: '标题'
    }
  },
  methods: {
    handleCancel() {
      this.$emit('onCancel')
    },
    handleOk() {
      this.$emit('onOk')
    }
  }
}
</script>